<template>
  <div class="wheel" :class="wheelClass">
    <img class="wheel-img" :style="wheelStyle" :src="wheelImage">
    <img class="point-img" :src="pointImage" @click.stop="clickPoint">
  </div>
</template>

<script>
// æ½å¥å¤§è½¬çç»ä»¶ @author zzt
export default {
  name: 'GiftWheel',
  props: {
    // å¯ä»¥è®¾ç½®è½¬çåå®¹æ ·å¼çç±»å
    wheelClass: {
      type: String,
      default: ''
    },
    // è½¬çå¾çå°å
    wheelImage: {
      type: String,
      default: ''
    },
    // æéå¾çå°å
    pointImage: {
      type: String,
      default: ''
    },
    // æ¯å¦æè½¬
    rotating: {
      type: Boolean,
      default: false
    },
    // é¢è½¬å¨çåæ°
    turns: {
      type: Number,
      default: 10
    },
    // ä¸­å¥çº§å«
    level: {
      type: [String, Number],
      default: ''
    },
    // ä¸­å¥åºåæ°ç»
    levelList: {
      type: Array,
      default: () => []
    },
    // æ´ä¸ªæè½¬çæ¯«ç§æ°
    rotateTime: {
      type: Number,
      default: 5
    },
    // è½¬çæ¯å¦å¤åå°åå§ä½ç½®
    reset: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      wheelStyle: {
        transition: '',
        transform: 'rotate(0deg)'
      },
      rotateDeg: 0
    }
  },
  watch: {
    // çå¬è½¬çæ¯å¦å¼å§æ»å¨
    rotating: {
      handler(isRotating) {
        if (isRotating) this.rotateWheel()
      }
    },
    reset: {
      handler(isReset) {
        if (isReset) this.resetWheel()
      }
    }
  },
  methods: {
    // è®¡ç®æ»å±éè¦æè½¬çè§åº¦
    calcRotateDeg() {
      const OneTurnDeg = 360
      // è·åä¸­é´levelçç´¢å¼å¼
      const levelIndex = this.levelList.indexOf(this.level)
      if (levelIndex > -1) {
        // æåä¸åéè¦è½¬å¨çè§åº¦æ°
        const restDeg = OneTurnDeg - (OneTurnDeg / this.levelList.length) * (levelIndex + 0.5)
        this.rotateDeg = restDeg + OneTurnDeg * this.turns
      } else {
        throw new Error('current level is not in levelList')
      }
    },
    // è®©è½¬çè½¬å¨èµ·æ¥
    rotateWheel() {
      this.calcRotateDeg()
      this.wheelStyle.transition = `transform ${this.rotateTime}s ease`
      this.$nextTick(() => {
        this.$emit('start-rotating')
        this.wheelStyle.transform = `rotate(${this.rotateDeg}deg)`
        this.$nextTick(() => {
          setTimeout(() => {
            this.$emit('stop-rotating')
          }, this.rotateTime * 1000)
        })
      })
    },
    // è®©è½¬çå¤å
    resetWheel() {
      this.wheelStyle.transition = ''
      this.wheelStyle.transform = 'rotate(0deg)'
    },
    clickPoint() {
      this.$emit('click-point')
    }
  }
}

</script>

<style scoped lang="scss">
.wheel {
    position: relative;
    width: 100%;
    height: 100%;

    .wheel-img {
        width: 100%;
        height: 100%;
    }

    .point-img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 2.026667rem;
        height: 2.533333rem;
    }
}
</style>
